header, header > div {
  max-height: 10vh;
  display: flex;
  flex-direction: row;
  align-content: center;
  align-items: center;
  font-size: calc(10px + 2vmin);
  padding: 5px;
}

header {
  position: sticky;
  top: 0;
  left: 0;
  max-width: 100%;
  z-index: 99999;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

header, .innerDrawer, #main {
  background-color: #404040;
}

.innerDrawer {
  padding: 20vh 2vw 2vh 2vw;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
}

.innerDrawer .increment {
  margin-bottom: 6vh;
}

.logo span {
  font-size: 16px;
  height: fit-content;
  margin-left: 13px;
  font-weight: 600;
}

header * {
  display: flex;
}

header .row {
  margin-bottom: 0;
}

.logo {
  color: #56E5E5;
}

.textSelections {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.textSelections > .increment:first-of-type {
  margin-right: 10vw;
}

.partialGoodsContainer {
  padding-right: 1vw;
}

.partialGoods * {
  font-size: 14px !important;
}

body {
  background-image: url('https://hayday.com/graphics/misc/overview_picture.webp');
  color: white;
  background-size: cover;
  font-family: Lobster,Georgia,serif;
  background-attachment: fixed;
}

/* Hide scrollbar for Chrome, Safari and Opera */
body::-webkit-scrollbar {
  display: none;
}

/* Hide scrollbar for IE, Edge and Firefox */
body {
  -ms-overflow-style: none;  /* IE and Edge */
  scrollbar-width: none;  /* Firefox */
}

#main {
  color: white;
  height: 96%;
  margin: 2% 10%;
  padding: 2vh 2vw;
  z-index: 2;
}

.increment {
  align-items: center;
}

.increment > div {
  width: 7.5rem !important;
}

.increment button {
  padding: 0;
}

.increment * {
  color: inherit !important;
}

/* Chrome, Safari, Edge, Opera */
.increment input::-webkit-outer-spin-button,
.increment input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
.increment input[type=number] {
  -moz-appearance: textfield;
  text-align: center;
}



.flexContainer {
  display: flex;
  justify-content: space-evenly;
  flex-direction: row;
  flex-wrap: wrap;
}

.flexContainer > * {
  display: flex;
}

.pictureTableRow {
  display: flex;
  justify-content: center;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
}

.pictureTableRow > * {
  display: flex;
}

.pictureTableRow > *:first-child {
  margin-right: 1.5vh;
}

.pictureTableRow > a {
  /*
  width: 30px;
  height: 30px;
   */
  margin-right: 1vh;
}

a.noshow {
  text-decoration: none;
  color: inherit;
}

.verticalFlexContainer {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-content: start;
}

.floatContainer {
  display: flex;
  flex-direction: column;
  margin: 0 2vw 4vh 2vw;
  align-content: start;
}

.row {
  margin-bottom: 4vh;
}

.innerRow > * {
  margin-bottom: 4vh;
}

.MuiDivider-root:last-child {
  display: none;
}

.selections {
  width: 100%;
  color: white;
}

@media (max-width: 1199px) {
  #main {
    margin-left: 3%;
    margin-right: 3%;
  }
}

#tabs {
  display: flex;
  justify-content: center;
  align-items: center;
  color: inherit;
  margin-top: 1vh;
  margin-bottom: 5vh;
}

#tabs > button {
  color: inherit;
  border-color: white;
  width: 30%;
  max-width: 400px;
  font-family: inherit;
}

#tabs > button:disabled {
  color: black;
}

.buttonContainer > button {
  color: inherit;
  border-color: white;
}

.fpButtonContainer > button {
  color: #56E5E5;
  border-color: #56E5E5;
}

.smallTitle {
  margin-top: 0;
  margin-bottom: .5vh;
}

.crop {
  width: 14em;
  margin: 0 1vw 4vh 0;
}

.crop .pictureTableRow {
  justify-content: flex-start;
}

.scrollToTop {
  position: sticky !important;
  bottom: 2vh;
  left: calc(99vw - 40px);
  z-index: 9999;
  border-radius: 360px !important;
  background-color: #56E5E5 !important;
}

.hide, .hide * {
  display: none !important;
}

html {
  scroll-behavior: smooth;
}

.tocButton {
  color: wheat !important;
  position: absolute !important;
  margin-left: 3vw !important;
}

.toc {
  margin-bottom: 3.5vh;
  padding-top: .5vh;
}

.toc > :first-child {
  margin-left: calc(3vw + 3rem) !important;
}

.toc > * {
  margin: .25em .25em !important;
  color: wheat !important;
}

@media (min-width: 801px) {
  header {
    min-height: 50px;
  }
}
@media (min-width: 701px) {
  .columnMayWrap {
    margin-left: 4vh;
  }
}

@media (max-width: 500px) {
  .selections > *:not(:last-child) {
    margin-bottom: 2vh;
  }
}

@media (max-width: 600px) {
  #tabs {
    flex-direction: column;
  }

  #tabs > button {
    width: 80%;
  }
}

@media (max-width: 700px) {
  .mayWrap {
    justify-content: center !important;
    margin-bottom: 1vh;
    margin-top: 1vh;
  }
  .columnMayWrap {
    margin-left: 0;
  }
}

@media (max-width: 1324px) {
  .floatContainer:first-child {
    margin-top: 0;
  }
}